.row {

  &:after {
    clear: both;
    content: "";
    display: table;
  }

}

.col {

  &-4-12 {
    float: left;
    width: 33.33%;
  }

  &-6-12 {
    float: left;
    width: 50%;
  }

  &-8-12 {
    float: left;
    width: 66.66%;
  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  .col {

    &-4-12 {
      width: 50%;
    }

    &-6-12 {
      width: 50%;
    }

    &-8-12 {
      width: 50%;
    }

  }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {
  .col {

    &-4-12 {
      width: 100%;
    }

    &-6-12 {
      width: 100%;
    }

    &-8-12 {
      width: 100%;
    }

  }

}

