.header {
  display: flex;
  flex-direction: column;

  &__bg {
    position: relative;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
    background: #0079e4; /* Old browsers */
    background: -moz-linear-gradient(left, #0079e4 0%, #1852d3 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #0079e4 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #0079e4 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */

    &:after {
      position: absolute;
      content: "";
      background: url("../images/cube.svg") no-repeat;
      top: 0;
      right: 0;
      width: 480px;
      height: 270px;
    }

  }

  &__h1-slogan {
    margin: 0;
    padding: 110px 0 30px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 36px;
    color: #fff;
    text-align: center;

  }

  &__p-slogan {
    margin: 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 19px;
    color: #fff;
    text-align: center;
  }

  &__form {
    width: 240px;
    margin: 0 auto;
    padding: 90px 0 110px 0;

    & input {
      width: 100%;
    }

    & button {
      width: 100%;
      color: #333333;
      background: #ffcb31;
    }

    & a {
      color: #fff;
    }

  }

}

