.scope {
  padding: 90px 0;

  &__title {
    font-family: 'Open Sans Condensed', sans-serif;
    text-align: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    width: 33.33%;
    display: flex;
  }

  &__item-icon {
    width: 40px;
    display: flex;
    align-items: center;

    &_soon {
      position: relative;

      &:after {
        position: absolute;
        content: "";
        background: url("../images/logo.png") no-repeat;
        top: 0;
        left: -10px;
        width: 34px;
        height: 34px;
      }

    }

  }

  &__item-description {
    width: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Open Sans Condensed', sans-serif;

    & h2 {
      margin: 0;
      font-weight: 700;
      font-size: 18px;
    }

    & p {
      margin: 0;
      font-weight: 300;
      font-size: 18px;
    }

  }

}
