.navbar {
  padding: 13px;

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__logo {
    width: 33px;
    height: 26px;
  }

  &__tog {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
  }

  &__menu {
    display: flex;
    flex-direction: row;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 18px;
    list-style: none;

    & li {
      margin: 0 10px;

      & a {
        text-decoration: none;
      }

    }

    &_ml {
      margin-left: auto;
    }

    &_mr {
      margin-right: auto;
    }

  }

}
